
<template>
 <div>
 	 <el-select class="v-select" v-model="value" :placeholder="tis">
    <el-option
      v-for="item in selectData"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
 </div>
</template>
<script>
  export default {
    data() {
      return {
        value: ''
      }
    },
	  props:{
			selectData:{},
		  	tis:{}
		}
  }
</script>
<style scoped>
	.v-select{
		width: 170px;
	}	
</style>


