import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Zhuce from '@/components/page/zhuce'
//import money from '@/components/page/money'
import money from '@/components/page/money'
import cuowujilu from '@/components/page/money/cuowujilu'
Vue.use(Router)

export default new Router({
  routes: [
      
    { path: '/',  component: Hello },
//   { path: '/homer',  component: Hello },
//   { path: '/catage',  component: catage },
//   { path: '/fleet',  component: fleet },
//   { path: '/incoive',  component: incoive },
//   { path: '/manage',  component: manage },
     { path: '/money',  component: money},
     { path: '/money/cuowujilu',  component:cuowujilu },
//  { path: '/operation',  component: operation },
    {
      path: '/zhuce',
      name: 'zhuce',
      component: Zhuce
    }
  ]
})



