<template>
<div>
	<div class="block">
		<el-date-picker
		  v-model="value2"
		  align="right"
		  type="date"
		  placeholder="选择日期"
		  :picker-options="pickerOptions1"
		  @change="change()">
		</el-date-picker>
    </div>
    {{value2}}
</div>
</template>

<script>
  export default {
    data() {
      return {
         pickerOptions1: {
          shortcuts: [
			  {
            text: '今天',
				onClick(picker) {
				  picker.$emit('pick', new Date());
				}
          }, 
		{
            text: '昨天',
				onClick(picker) {
				  const date = new Date();
				  date.setTime(date.getTime() - 3600 * 1000 * 24);
				  picker.$emit('pick', date);
				}
          }, 
		{
            text: '一周前',
				onClick(picker) {
				  const date = new Date();
				  date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
				  picker.$emit('pick', date);
				}
          }]
        },
        value2: '',
      };
    }
  };
</script>
<style scoped>
	.block{
		width: 170px;
	}
	.el-date-editor.el-input{
		width: 100%;
	}
</style>


