<template>
	<div>
		
		
		
			<headaa></headaa>
			
			<div class="money-wrapper">
			
			<div class="money-shuoming clear">
				<h4>付款记录 &nbsp;/</h4>
				<span>说明说明说明说明说明说明</span>
				<div>
					<router-link to="/money/cuowujilu">
						<el-button>查看错误记录</el-button>
					</router-link>
					<el-button class="xecle">EXCEL导出</el-button>
				</div>
			</div>
			
			<div class="money-content">
				<div class="clear">
					<div v-for="i in newData" class="addNew">
						<span>{{i.sp}}</span>
						<inputs :tis="i.inp" class="inp"></inputs>
						<span v-if="i.ps">{{i.ps}}</span>
					</div>
					<div class="addNew">
						<span>申请时间</span>
						<dates class="inp"></dates>
					</div>
					<div class="addNew" id="addNew">
						<span>支付时间</span>
						<dates class="inp"></dates>
						<span>本日 本周</span>
					</div>
					<div class="addNew">
						<span>本页显示数</span>
						<selects class="inp" :selectData="options1"></selects>
					</div>
					<div class="addNew">
						<span>支付单号</span>
						<inputs tis="请填写支付单号" class="inp"></inputs>
					</div>
					<div class="addNew">
						<span>支付状态</span>
						<selects class="inp" :selectData="options2"></selects>
					</div>
					<div class="addNew">
						<el-button type="primary" class="btn" size="large">查询</el-button>
					</div>
				</div>
				
				<div class="tab">
					<div id="div">
						<span>
							未通过：
							<i>0.00</i>
							元
						</span>
						<span>
							申请中：
							<i>0.00</i>
							元
						</span>
						<span>
							已开票：
							<i>122334334.00</i>
							元
						</span>
						<span class="tab-last">
							合计：
							<i>122334334.00</i>
							元
						</span>
					</div>
						
					<template>
					  <el-table :data="tableData"  border  style="width: 100%">
					  	<el-table-column type="selection" width="55"></el-table-column>
					  	
					    <el-table-column prop="fnum" label="支付单号"  width="120px"></el-table-column>
					    <el-table-column  prop="ynum" label="运单号" width="120px"></el-table-column>
					    <el-table-column prop="sname" label="司机姓名"  width="100px"></el-table-column>
					    <el-table-column  prop="snum" label="司机手机号" width="130px"></el-table-column>
					    <el-table-column prop="cnum" label="车牌号" width="120px"></el-table-column>
					    <el-table-column prop="price" label="运费金额"  width="100px"></el-table-column>
					    <el-table-column  prop="pay" label="支付方式" width="100px"></el-table-column>
					    <el-table-column prop="payn" label="支付状态" width="100px"></el-table-column>
					    <el-table-column prop="sper" label="申请人"  width=""></el-table-column>
					    <el-table-column  prop="fper" label="付款人" width=""></el-table-column>
					    <el-table-column prop="gotime" label="申请时间" width="200px"></el-table-column>
					    <el-table-column prop="totime" label="申请时间"  width="200px"></el-table-column>
					    <el-table-column  prop="todo" label="操作" width="160px" >
					    	<template scope="scope">
					    		<router-link to=""><span style="color: dodgerblue;">{{ scope.row.todo[0]}}&nbsp;|</span></router-link>
					    		<router-link to=""><sapn style="color: dodgerblue;">{{ scope.row.todo[1]}}&nbsp;|</sapn></router-link>
					    		<router-link to=""><sapn style="color: dodgerblue;"><el-button type="text" @click="dayin()">{{ scope.row.todo[2]}}</el-button></sapn></router-link>
					    	</template>
					    </el-table-column>
					  </el-table>
					</template>

					<div class="page ">
						<div class="page-left">
							<span>操作功能：</span>
							 <el-button type="success" @click="open()">确认付款</el-button>
						</div>
						
						<div class="page-rigth"> 
							  <div class="block">
								  <el-pagination layout="prev, pager, next" :total="80"></el-pagination>
							  </div>
						</div>
					</div>
					
				</div>

			</div>
			
		</div>
		
			<div class="bigWrap" >
				<div class="open">
					<div class="clear top">
						<span>运费支付</span>
						<span @click="close()" class="el-icon-close"></span>
					</div>
					<table border="1" cellspacing="0">
					<tr>
						<td rowspan="2">支付方式</td>
						<td>
							 <el-radio class="radio" v-model="radio" label="1">
							 	保理支付可用额度：<span>1200000.00元</span>
							 </el-radio>

							
						</td>

					</tr>
					<tr>
						<td colspan="2">
							<el-radio :label="2" class="radio" v-model="radio">
								余额支付可用余额：<span>1200.00元</span>
							</el-radio>
							
						</td>
					</tr>
					<tr>
						<td colspan="3">
							本次支付运费合计：<span>1200.00元</span>
						</td>
					</tr>
					<tr>
						<td>输入支付密码</td>
						<td>
							<el-input class="btn" type="password"></el-input>
						</td>
					</tr>
					
				</table>
					<el-button type="success" :plain="true" class="quer" @click="close1()">确认支付</el-button>
				</div>
				
			</div>
		
	</div>
</template>

<script>
import headaa from '@/components/headaa'
import inputs from '@/components/input'
import selects from '@/components/select'
import dates from '@/components/date'
	export default {
	   data() {
     	 return {
			 radio: '1',
			 value: '',
			newData:[
				{'sp':'运单号','inp':'请输入运单号','ps':'批量'},
				{'sp':'司机姓名','inp':'请输入司机姓名'},
				{'sp':'司机手机号','inp':'请输入司机手机号'},
				{'sp':'车牌号','inp':'请输入车牌号'},
				{'sp':'收款人','inp':'选择'},
				{'sp':'申请人','inp':'选择'}
			],
			tableData:
			[
				{
		          fnum: '564132135',
		          ynum: '2011545356',
		          sname: '张师傅',
		          snum:'13588145678',
		          cnum:'鲁E974585',
		          price:'2111.99',
		          pay:'保理',
		          payn:'已申请',
		          sper:'张士豪',
		          fper:'张士豪',
		          gotime:'2016-12-23 10：20：30',
		          totime:'2016-12-23 10：20：30',
		          todo:['轨迹','短信','打印']
		         
		        },
		        {
		          fnum: '564132135',
		          ynum: '2011545356',
		          sname: '张师傅',
		          snum:'13588145678',
		          cnum:'鲁E974585',
		          price:'2111.99',
		          pay:'保理',
		          payn:'已申请',
		          sper:'张士豪',
		          fper:'张士豪',
		          gotime:'2016-12-23 10：20：30',
		          totime:'2016-12-23 10：20：30',
		          todo:['轨迹','短信','打印']
		          
		        }
			],
			options1:[
				{'value':'1','label':'10条'},
				{'value':'2','label':'20条'},
				{'value':'3','label':'30条'},
				{'value':'4','label':'40条'},
				{'value':'5','label':'50条'}
			],
			options2:[
				{'value':'1','label':'已支付'},
				{'value':'2','label':'未支付'},
				{'value':'3','label':'完成'}
			]
			
		}
	
    },
   
	

	
    components:{
	  	headaa,
	  	inputs,
	  	selects,
	  	dates
	},
	methods:{
		
		open(){
//			alert(1)
			let oDiv=document.querySelector(".bigWrap");
			oDiv.style.display="block";
		},
		
		close(){
//			alert(2)
			let oDiv=document.querySelector(".bigWrap");
			oDiv.style.display="none";
		},
		close1(){
			 this.$message({
	          showClose: true,
	          message: '支付成功',
	          type: 'success'
	        });
	        let oDiv=document.querySelector(".bigWrap");
			oDiv.style.display="none";

		},
		 dayin() {
	        this.$confirm('是否要打印该信息?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '打印成功!'
		          });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消'
		          });          
		        });

	      }

		
	}
	 

	  
	  
}
</script>

<style scoped lang="less">
.bigWrap{
	width: 100%;
	height: 200%;
	background: rgba(0,0,0,0.5);
	position: absolute;
	top:0;
	left:0;
	z-index: 3;
	display: none;
	.open{
		width: 600px;
		height: 400px;
		background: #fff;
		position: absolute;
		top:50%;
		left:50%;
		margin-top: -200px;
		margin-left:-300px ;
		.top{
			font-size: 18px;
			border-bottom:1px solid #ccc;
			padding:10px 20px ;
			span:nth-of-type(1){
				float: left;
			}
			span:nth-of-type(2){
				float: right;
			}
		}
		table{
			width: 550px;
			height:260px ;
			text-align: center;
			margin: 20px 30px;
			.btn{
				width: 200px;
			}
			span{
				color: orangered;
			}
		}
		.quer{
			float: right;
			margin-right: 30px;
		}
	}
}
.money-wrapper{
	width: 1200px;
	height:auto;
	margin: 0 auto;
	.money-shuoming{
		margin:30px 0;
		h4{
			font-size: 20px;
			color:#3954A5 ;
			float: left;
		}
		span{
			color: #8896B0;
			float: left;
			line-height: 26px;
			margin-left: 20px;
		}
		div{
			float: right;
			.xecle{
			border-color:#679C68 ;
			color: #679C68;
			}
		}
		
	}
	.money-content{
		border:1px solid #E6E9F0;
		height:800px;
		.clear{
			margin: 30px 0px;
		}
		.addNew{
			width: 280px;
			float: left;
			line-height: 40px;
			margin: 10px 5px;
			.inp,span{
				float: left;
			}
			span:nth-of-type(1){
				font-size: 16px;
				color: #8391B8;
				width: 80px;
			}
			span:nth-of-type(2){	
				color: #4267B8;
			}
			.btn{
				width: 200px;
				background:#3052AC;
				color: #fff;
				float: right;
			}
		}
		#addNew{
			width: 310px;
			margin: 10px 0;
		}
		.tab{
			border-top: 2px solid #E8E8F0;
			width: 96%;
			height: auto;
			margin: 0 auto;
			/*background: pink;*/
			#div{
				line-height: 30px;
				background: #FFFCFD;
				border:1px solid #F5EFEF;
				margin:10px 0;
				span{
					margin-right:20px ;
				}
				i{
				font-style: normal;
				color: #BA7A71;
				}
				.tab-last{
					float: right;
				}
				
			}
			.el-table .cell{
				text-align: center;
			}
			
		}
		.page{
			margin-top: 10px;
			.page-left{
				float: left;
			}
			.page-rigth{
				float: right;
			}
		}
		
	}
	
	
}
</style>


