<template>
	<div class="">
		
		<headaa></headaa>
		
		<div class="money-wrapper">
			
			<div class="money-shuoming clear">
				<h4>错误记录 &nbsp;/</h4>
				<span>说明说明说明说明说明说明</span>
				<div>
					<router-link to="/money">
						返回上一页
					</router-link>
				</div>
			</div>
			
			<div class="money-content">
				
				<div class="clear">
					<div class="addNew" >
						<span>支付单号</span>
						<inputs class="inp" tis="请输入订单号"></inputs>
					</div>
					<div class="addNew" >
						<span>运单号</span>
						<inputs class="inp" tis="选择下单区间"></inputs>
					</div>
					<div class="addNew" >
						<span>错误时间</span>
						<dates class="inp" tis="选择"></dates>
					</div>
					<div class="addNew" >
						<span>处理状态</span>
						<selects class="inp" tis="选择" :selectData="options"></selects>
					</div>
					<div class="addNew">
						<el-button type="primary" class="btn" size="large">查询</el-button>
					</div>
				</div>
				
				
				<div class="tab">
					
					<template>
					  <el-table :data="tableData"  border  style="width: 100%">
					  	
					    <el-table-column prop="fnum" label="支付单号"  width="120px"></el-table-column>
					    <el-table-column  prop="ynum" label="运单号" width="120px"></el-table-column>
					    <el-table-column prop="sname" label="司机姓名"  width="100px"></el-table-column>
					    <el-table-column  prop="snum" label="司机手机号" width="140"></el-table-column>
					    <el-table-column prop="cnum" label="支付金额" width="100px"></el-table-column>
					    <el-table-column prop="gotime" label="申请时间" width="200px"></el-table-column>
					    <el-table-column prop="totime" label="付款时间"  width="200px"></el-table-column> 
					    <el-table-column prop="price" label="失败原因"  width="100px"></el-table-column>
					    <el-table-column  prop="pay" label="失败时间" width="200px"></el-table-column> 
					    <el-table-column  prop="todo" label="操作" width="80px" >
					    	<template scope="scope">
					    		<router-link to=""><sapn style="color: dodgerblue;"><el-button type="text" @click="dayin()">{{ scope.row.todo}}</el-button></sapn></router-link>
					    	</template>
					    </el-table-column>
					    
					  </el-table>
					</template>
				</div>
				
			</div>
			
			
		</div>
		
	</div>
</template>

<script>
import headaa from '@/components/headaa'
import inputs from '@/components/input'
import dates from '@/components/date'
import selects from '@/components/select'
export default{
	
	data(){
		return{
			options:[
				{'value':'1','label':'待处理'},
				{'value':'2','label':'未处理'},
				{'value':'3','label':'已处理'}
			],
			tableData:
			[
				{
		          fnum: '564132135',
		          ynum: '2011545356',
		          sname: '张师傅',
		          snum:'13588145678',
		          cnum:'1200.00',
		          price:'账号错误',
		          pay:'2016-12-23 10：20：30',
		          gotime:'2016-12-23 10：20：30',
		          totime:'2016-12-23 10：20：30',
		          todo:'打印'
		         
		        }
			]
		}
	},
	 components:{
	  	headaa,
	  	inputs,
	  	dates,
	  	selects
	},
	methods:{
		 dayin() {
	        this.$confirm('是否要打印该信息?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '打印成功!'
		          });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消'
		          });          
		        });

	      }
	}
	
}
</script>

<style scoped lang="less">
.money-wrapper{
	width: 1200px;
	height:auto;
	margin: 0 auto;
	.money-shuoming{
		margin:30px 0;
		h4{
			font-size: 20px;
			color:#3954A5 ;
			float: left;
		}
		span{
			color: #8896B0;
			float: left;
			line-height: 26px;
			margin-left: 20px;
		}
		div{
			float: right;
			margin-right: 20px;
		}
		
	}
	.money-content{
		border:1px solid #E6E9F0;
		height:800px;
		.clear{
			margin: 30px 0px;
		}
		.addNew{
			width: 280px;
			float: left;
			line-height: 40px;
			margin: 10px 5px;
			.inp,span{
				float: left;
			}
			span{
				font-size: 16px;
				color: #8391B8;
				width: 80px;
			}
			.btn{
				width: 200px;
				background:#3052AC;
				color: #fff;
				margin-left: 365px;
			}
		}
		.tab{
			border-top: 2px solid #E8E8F0;
			width: 96%;
			height: auto;
			margin: 0 auto;
			padding-top: 30px;
		}
	}
}
</style>


