<template>
	<div>
		<el-input class="v-input" v-model="input" :placeholder="tis"></el-input>
	</div>
</template>

<script>
export default {
  data() {
    return {
      input: ''
    }
  },
	props:{
		tis:{}
	}
}
</script>
<style scoped>
	.v-input{
		width: 170px;
	}
</style>


